export default {
	data() {
		return {
			more: false,
			pageNum: 1,
			pageSize: 10,
			loading: true,
			list: []
		};
	},
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getCoupon(id,i){
			this.$Fetch({
				url:"Coupon/getCoupon",
				data:{id:id},
				success:res=>{
					this.list.splice(i,1);
					uni.showModal({
						content:"兑换成功",
						confirmText:"继续兑换",
						cancelText:"返回",
						success:function(ret){
							if(ret.cancel){
								uni.navigateBack({
									
								})
							}
						}
					})
				}
			})
		},
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'Coupon/getData',
					data: { pageNum: this.pageNum ,pageSize:this.pageSize},
					success: res => {
						console.log(res)
						if (res.length > 0) {
							this.list = this.list.concat(res);
							if (res.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						}
					}
				});
			}
		}
	}
};
